import { Mesh, NodeDirection, Node, RouteDefinition } from './../models/interfaces';
import { Point } from '../models/interfaces';

export class PathFinder {

    constructor() {
        console.log('Initializing...');
    }

    getDistance(pA: Point, pB: Point): number {
        const exp1 = Math.pow(pA.X - pB.X, 2);
        const exp2 = Math.pow(pA.Y - pB.Y, 2);

        return Math.sqrt(exp1 + exp2);
    }

    mountPath(mesh: Mesh, route: RouteDefinition, ): Node[] {
        const path: Node[] = [];
        let current_node: Node = route.origem;
        while (current_node !== route.destino) {
            const directions: NodeDirection[] = this.getPossibleRouteNodes(mesh, route);
            current_node = this.getNextNodeRoute(mesh, current_node, route.destino, directions);
            path.push(current_node);
        }
        return path;
    }

    getPossibleRouteNodes(mesh: Mesh, route: RouteDefinition, previous_node?: Node): NodeDirection[] {
        const routes: Node[] = [];
        const possible_directions: NodeDirection[] = [];
        if (route.origem.location.Y !== 0) {
            possible_directions.push(NodeDirection.N);
        }
        if (route.origem.location.X !== 0) {
            possible_directions.push(NodeDirection.O);
        }
        if (route.origem.location.Y !== (mesh.n_collumns - 1)) {
            possible_directions.push(NodeDirection.S);
        }
        if (route.origem.location.X !== (mesh.n_rows - 1)) {
            possible_directions.push(NodeDirection.L);
        }

        if (previous_node) {
            // ultima posição
            let last_directon: NodeDirection;
            if (previous_node.location.X === route.origem.location.X) {
                if (previous_node.location.Y < route.origem.location.Y) {
                    last_directon = NodeDirection.N;
                } else {
                    last_directon = NodeDirection.S;
                }
            } else {
                if (previous_node.location.X < route.origem.location.X) {
                    last_directon = NodeDirection.O;
                } else {
                    last_directon = NodeDirection.L;
                }
            }
            possible_directions.splice(possible_directions.indexOf(last_directon), 1);
        }

        return possible_directions;
    }

    getNextNodeRoute(mesh: Mesh, origem: Node, destino: Node, directions: NodeDirection[]) {
        let next_node: Node = null;
        let nodes: Node[] = [];
        if (directions.length === 1) {
            next_node = this.getNodeByDirection(mesh, origem, directions[0]);
        } else {
            for (const direc of directions) {
                nodes.push(this.getNodeByDirection(mesh, origem, direc));
            }
        }
        next_node = nodes.splice(0, 1)[0];
        let last_distance = this.getDistance(next_node.location, destino.location)

        for (const node of nodes) {
            const distance = this.getDistance(node.location, destino.location);
            if (distance < last_distance) {
                last_distance = distance;
                next_node = node;
            }
        }

        return next_node;
    }

    getNodeByDirection(mesh: Mesh, node: Node, direction: NodeDirection): Node {
        let _node: Node;
        switch (direction) {
            case NodeDirection.N:
                _node = mesh.rows[node.location.X][node.location.Y - 1];
                break;
            case NodeDirection.S:
                _node = mesh.rows[node.location.X][node.location.Y + 1];
                break;
            case NodeDirection.L:
                _node = mesh.rows[node.location.X + 1][node.location.Y];
                break;
            default:
                _node = mesh.rows[node.location.X - 1][node.location.Y];
                break;
        }
        return _node;
    }
}
