import { Mesh, ComponentStatus, Node, RouteDefinition, NodeDirection, NodeClass } from './../models/interfaces';
import { Component } from '@angular/core';
import { NgbModal, ModalDismissReasons, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { PathFinder } from '../core/path-finder';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  // modal dependences
  closeResult: string;

  hide_presentation = false;
  title = 'Malha Hídrica Inteligente';

  mesh: Mesh;

  // dados de inicialização da malha
  mesh_rows = 10;
  mesh_cols = 10;

  // loading state
  loading = {
    show: false,
    percent: 0
  };

  // Malha processada
  ready_mesh = false;

  // node pre-selecionado
  pre_selected_node: Node = null;

  // node selecionado
  selected_nodes: RouteDefinition;

  // variavel select tipo de no
  select_tipo_no = '';

  // instância da modal
  modal: NgbModalRef;

  path_finder: PathFinder;

  // path atual
  current_path: Node[] = [];
  // ultimo path
  last_path: Node[] = [];

  constructor(private modalService: NgbModal) {
    this.selected_nodes = {
      origem: null,
      destino: null
    };
    this.mesh = {
      n_rows: this.mesh_rows,
      n_collumns: this.mesh_cols,
      rows: []
    };
    this.path_finder = new PathFinder();
  }

  startSimulator(event) {
    this.hide_presentation = true;
    setTimeout(() => {
      this.startLoading();
    }, 200);
  }

  startLoading() {
    this.loading.show = true;
    const timer = setInterval(() => {
      if (this.loading.percent < 100) {
        this.loading.percent += 10;
      } else {
        clearInterval(timer);
        setTimeout(() => {
          this.loading.show = false;
          this.initMesh();
          console.log(this.mesh);
        }, 200);
      }
    }, 100);
  }

  initMesh() {
    for (let row = 0; row < this.mesh_rows; row++) {
      let column = [];
      for (let col = 0; col < this.mesh_cols; col++) {
        const node = {
          location: { X: row, Y: col },
          state: ComponentStatus.FUNCIONAL,
          class: NodeClass.DEFAULT_NODE
        };
        column.push(node);
        // if (row === 0) {
        //   this.mesh.n_collumns++;
        // }
      }
      this.mesh.rows.push(column);
      // this.mesh.n_rows++;
    }
    this.ready_mesh = true;
  }

  selectNode() {
    if (this.select_tipo_no === 'ORIGEM') {
      if (this.selected_nodes.destino === this.pre_selected_node) {
        this.selected_nodes.destino.class = NodeClass.DEFAULT_NODE;
        this.selected_nodes.destino = null;
      }
      this.pre_selected_node.class = NodeClass.PATH_ORIGIN;
      if (this.selected_nodes.origem !== null) {
        this.selected_nodes.origem.class = NodeClass.DEFAULT_NODE;
      }
      this.selected_nodes.origem = this.pre_selected_node;
    } else if (this.select_tipo_no === 'DESTINO') {
      if (this.selected_nodes.origem === this.pre_selected_node) {
        this.selected_nodes.destino.class = NodeClass.DEFAULT_NODE;
        this.selected_nodes.origem = null;
      }
      if (this.selected_nodes.destino !== null) {
        this.selected_nodes.destino.class = NodeClass.DEFAULT_NODE;
      }
      this.pre_selected_node.class = NodeClass.PATH_DESTINY;
      this.selected_nodes.destino = this.pre_selected_node;
    }
    // this.pre_selected_node = null;
    this.modal.close();

    if (this.selected_nodes.origem != null && this.selected_nodes.destino != null) {
      this.mountRoute();
    }
  }

  mountRoute() {
    this.current_path = this.path_finder.mountPath(this.mesh, this.selected_nodes);
    this.colorPath();
    // console.log(route);
  }

  colorPath() {
    for (const node of this.last_path) {
      node.class = NodeClass.DEFAULT_NODE;
    }
    this.last_path = this.current_path;
    for (const node of this.current_path) {
      if (this.selected_nodes.destino !== node) {
        node.class = NodeClass.PATH_NODE;
      }
    }
  }

  cancelSelectNode(callback) {
    this.modal.close();
    // this.pre_selected_node = null;
  }

  // classe css para os nós da rota
  getNodeCssClass(node: Node): string {
    let css_class = '';
    if (node === this.selected_nodes.origem) {
      css_class = 'origin-node';
    } else if (node === this.selected_nodes.destino) {
      css_class = 'destiny-node';
    }
    return css_class;
  }

  // modeal functions
  open(content, node) {
    this.pre_selected_node = node;
    this.modal = this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
    this.modal.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

}
