export interface Point {
    X: number;
    Y: number;
}

export interface Entrada {
    estado: 'Aberto' | 'Fechado';
    open: () => {};
    close: () => {};
}

export enum ComponentStatus {
    FUNCIONAL = 'FUNCIONAL',
    NAO_FUNCIONAL = 'NAO FUNCIONAL',
}

export enum NodeDirection {
    N = 'NORTE',
    S = 'SUL',
    L = 'LESTE',
    O = 'OESTE',
}

export interface Node {
    location: Point;
    entries?: Entrada[];
    state: ComponentStatus;
    class: NodeClass;
    // close_entry: (index: number) => {};
    // open_entry: (index: number) => {};
}

export enum NodeClass {
    PATH_ORIGIN = 'origin-node',
    PATH_DESTINY = 'destiny-node',
    PATH_NODE = 'path-node',
    DEFAULT_NODE = '',
}

// Malha
export interface Mesh {
    n_rows: number;
    n_collumns: number;
    rows: Node[][];
}

export interface RouteDefinition {
    origem: Node;
    destino: Node;
}
